  data () {
    return {
      navlist:['Home','HRC','Scenes','Architecture','Blog','Character','Broker'],
      navnum:0,
      imgheight:null,
    }
  },
  created() {
    let useragent=navigator.userAgent.toLowerCase()
    let ismobile = /(iphone|ipod|ipad|android)/.test(useragent)
    if(ismobile){
      this.$router.push({path: '/m'})
    }
  },
  mounted() {
    let s=document.body.clientHeight;
    this.imgheight=s
  },
  methods:{
    toblog(){
      this.$router.push({path: '/blog'})
    },
    more(){
      $("html,body").animate({
          scrollTop: $(`#nav1`)[0].offsetTop
      }, 500);
    },
    rollclick(index){
      this.navnum = index
      let mtop = $(`#nav` + index)[0].offsetTop
      $("html,body").animate({
          scrollTop: mtop
      }, 500);
    }
  }
}
</script>

